import React from 'react'
import styled, { css } from 'styled-components'
import { Container, Row, Col } from 'react-awesome-styled-grid'
import siteConfig from '../../data/siteConfig'
import { withPrefix } from "gatsby"
import loadable from '@loadable/component'

import Hero from '../components/hero'
import SEO from '../components/SEO'
import Wrapper from '../components/wrapper'

const Layout = loadable(() => import('../components/layout'))

const Image = styled.img`
  max-height: 220px;
  max-width: 220px;
  object-fit: cover;
  object-position: center center;
  border-radius: 10px;
  box-shadow: 24px 47px 79px -21px rgba(0,0,0,0.51);
`

const JobCard = styled.a`
  max-width: 220px;
  text-align: center;
  text-decoration: none;
  color: inherit;

  ${({ href }) => href && css`
    &:hover ${Image}{
      transition: transform .5s;
      transform: translateY(-5px);
    }
  `}
`

const Blog = ({ className, location }) => {
  const title = siteConfig.blogPresentation
  const subtitle = siteConfig.blogSubtitle
  const { keywords, blog } = siteConfig
  return (
    <Layout location={location}>
      <SEO
        title={title}
        keywords={keywords}
      />

      <Hero
        heroImg={withPrefix('/images/blog-cover.jpeg')}
        title={title}
        subtitle= {subtitle}
      />

      <Wrapper className={className}>
        <Container className="page-content" fluid>
          <Row>
            {blog.map(entry => (
              <Col
                key={entry.description}
                align="center"
              >
                <JobCard
                  as={entry.url ? "a" : "div"}
                  href={entry.url}
                  target="_self" /* originally _blank to open link in a new window*/
                >
                  <Image src={withPrefix(entry.image)} />
                  <p><strong>{entry.caption}</strong><br></br>
                  {entry.description}</p>
                </JobCard>
              </Col>
            ))}
          </Row>
        </Container>
      </Wrapper>
    </Layout>
  )
}

export default styled(Blog)`
  .page-content {
    max-width: 100%;
    margin-bottom: 40px;
  }

`
